<template>
  <div class="home">
    <br />

    <covidAlert />

    <h1>Covid-19 @NTPU</h1>
    <h5>Data from: <a href="http://lms.ntpu.edu.tw/board.php?courseID=50180&f=news" target="_bland"></a> </h5>
    <h4 style="color: red">{{ data_loading }}</h4>
    <v-divider />
    <br />

    <h2>List of data</h2>
    <br />
    <v-data-table
      :headers="headers"
      :items="cdc_data"
      :items-per-page="5"
      class="elevation-1"
    ></v-data-table>
    <br />
  </div>
</template>

<script>

import covidAlert from '@/components/covidAlert.vue'

export default {
  name: "HomeView",
  components: {
    covidAlert
  },
  data() {
    return {
      data_loading: "Loading data...",
      cdc_data: [],
      headers: [
        { text: '', value: 'date', align: 'center' },
        { text: ' - ', value: 'confirmedToday.studentTotal', align: 'center' },
        { text: ' -  - ', value: 'confirmedToday.studentZoneDetail.Taipei', align: 'center' },
        { text: ' -  - ', value: 'confirmedToday.studentZoneDetail.Sanxia', align: 'center' },
        { text: ' - ', value: 'confirmedToday.teacher', align: 'center' },
        { text: ' - ', value: 'confirmedToday.staff', align: 'center' },
        { text: ' - ', value: 'confirmedCase.studentTotal', align: 'center' },
        { text: ' -  - ', value: 'confirmedCase.studentZoneDetail.Taipei', align: 'center' },
        { text: ' -  - ', value: 'confirmedCase.studentZoneDetail.Sanxia', align: 'center' },
        { text: ' - ', value: 'confirmedCase.teacher', align: 'center' },
        { text: ' - ', value: 'confirmedCase.staff', align: 'center' },
        { text: ' - ', value: 'isolate.studentTotal', align: 'center' },
        { text: ' -  - ', value: 'isolate.studentZoneDetail.Taipei', align: 'center' },
        { text: ' -  - ', value: 'isolate.studentZoneDetail.Sanxia', align: 'center' },
        { text: ' - ', value: 'isolate.teacher', align: 'center' },
        { text: ' - ', value: 'isolate.staff', align: 'center' },
        { text: ' - 	', value: 'selfHealthManagement.studentTotal', align: 'center' },
        { text: ' -  - ', value: 'selfHealthManagement.studentZoneDetail.Taipei', align: 'center' },
        { text: ' -  - ', value: 'selfHealthManagement.studentZoneDetail.Sanxia', align: 'center' },
        { text: ' - ', value: 'selfHealthManagement.teacher', align: 'center' },
        { text: ' - ', value: 'selfHealthManagement.staff', align: 'center' },
        { text: ' - ', value: 'homeQuarantine.studentTotal', align: 'center' },
        { text: ' -  - ', value: 'homeQuarantine.studentZoneDetail.Taipei', align: 'center' },
        { text: ' -  - ', value: 'homeQuarantine.studentZoneDetail.Sanxia', align: 'center' },
        { text: ' - ', value: 'homeQuarantine.teacher', align: 'center' },
        { text: ' - ', value: 'homeQuarantine.staff', align: 'center' },
      ],
    };
  },
  methods: {
    get_dorm_status() {
      const api = "https://script.google.com/macros/s/AKfycbxZTWjZU1T-Ro0BQW5NLi8ewFSCNxv2NJTLoLpTXNmdMT0fXSCD9k6BeRY0Y8UrBnFN9g/exec";
      this.$axios
        .get(api)
        .then((resp) => {
          console.log(resp);
          this.cdc_data = resp.data;
          const updateTimestamp = new Date(this.cdc_data[0].dataTimestamp);
          this.data_loading = "Updated: " + updateTimestamp;
        })
        .catch((err) => {
          console.log(err);
          this.data_loading = err;
        });
    },
  },
  created: function () {
    this.get_dorm_status()
  },
};
</script>

